<template>
  <div class="Container-TotalTaskList">
    <!--  -->
    <div class="head-btn">
      <div class="oper-btns-left">
        <div class="select">
          <el-select
            v-model="selectvalue"
            placeholder=""
            filterable
            clearable
            @change="selectchange"
            style="margin-left:5px;"
          >
            <el-option-group
              v-for="group in selectoptions"
              :key="group.label"
              :label="group.label"
            >
              <el-option
                v-for="item in group.options"
                :key="item.value"
                :label="item.label"
                :value="item.value"
              >
              </el-option>
            </el-option-group>
          </el-select>
        </div>
        <div>
          <el-tag
            :key="tag"
            v-for="tag in dynamicTags"
            closable
            @close="handleClose(tag)"
            style="margin-left:5px;"
          >
            {{ tag }}
          </el-tag>
        </div>
        <div class="search">
          <el-button icon="el-icon-search" @click="search"></el-button>
        </div>
        <div class="refresh">
          <el-button icon="el-icon-refresh" @click="refresh"
            >
          </el-button>
        </div>
      </div>
      <div class="oper-btns-right" v-if="['ROOT', 'ADMIN'].includes(userRole)">
        <el-button
          class="bigdel-btn"
          icon="el-icon-delete"
          @click="delectExtraInfo"
          ></el-button
        >
      </div>
    </div>
    <!-- table -->
    <el-table
      ref="multipleTable"
      :data="tableData"
      stripe
      border
      style="width:100%;"
      class="extraTable"
      @selection-change="handleDetailSelectionChange"
    >
      <el-table-column type="selection"></el-table-column>
      <el-table-column prop="index" label="" width="50"></el-table-column>
      <el-table-column prop="devicename" label=""></el-table-column>
      <el-table-column prop="deviceNo" label=""></el-table-column>
      <el-table-column prop="taskname" label=""></el-table-column>
      <el-table-column prop="tasknumber" label=""></el-table-column>
      <el-table-column prop="nextDate" label=""></el-table-column>
      <el-table-column prop="opuser" label=""></el-table-column>
      <el-table-column prop="deadline" label=""></el-table-column>
      <el-table-column
        prop="setting"
        label=""
        width="200"
        v-if="['ROOT', 'ADMIN', 'CREATOR', 'SUPERVISOR'].includes(userRole)"
      >
        <template slot-scope="scope">
          <el-button @click="handleDetail(scope.$index, scope.row)"
            ></el-button
          >
          <el-button @click="handleEdit(scope.$index, scope.row)"
            ></el-button
          >
          <el-button @click="handleDelete(scope.$index, scope.row)"
            ></el-button
          >
        </template>
      </el-table-column>
      <el-table-column prop="setting" label="" width="160" v-else>
        <template slot-scope="scope">
          <el-button @click="handleDetail(scope.$index, scope.row)"
            ></el-button
          >
        </template>
      </el-table-column>
    </el-table>
    <!--  -->
    <div>
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="currentPage"
        :page-sizes="[5, 10, 15]"
        :page-size="page_size"
        layout="sizes,total, prev, pager, next, jumper"
        :total="total"
      ></el-pagination>
    </div>
    <!--  -->
    <el-dialog title="" :visible.sync="dialogSearchVisible" width="35%">
      <el-input
        v-model="selectmodel"
        placeholder=""
        v-if="selectvalue == 'name'"
      ></el-input>

      <!--  -->
      <el-select
        v-model="scheduleTypevalue"
        placeholder=""
        filterable
        clearable
        v-if="selectvalue == 'scheduleType'"
      >
        <el-option-group
          v-for="group in scheduleTypeoptions"
          :key="group.label"
          :label="group.label"
        >
          <el-option
            v-for="item in group.options"
            :key="item.value"
            :label="item.label"
            :value="item.value"
          >
          </el-option>
        </el-option-group>
      </el-select>

      <!--  -->
      <el-select
        v-model="devicevalue"
        placeholder=""
        filterable
        clearable
        multiple
        v-if="selectvalue == 'device'"
      >
        <el-option-group
          v-for="group in deviceoptions"
          :key="group.label"
          :label="group.label"
        >
          <el-option
            v-for="item in group.options"
            :key="item.value"
            :label="item.label"
            :value="item.value"
          >
          </el-option>
        </el-option-group>
      </el-select>

      <!--  -->
      <el-select
        v-model="opsvalue"
        placeholder=""
        filterable
        clearable
        multiple
        v-if="selectvalue == 'ops'"
      >
        <el-option-group
          v-for="group in opsoptions"
          :key="group.label"
          :label="group.label"
        >
          <el-option
            v-for="item in group.options"
            :key="item.value"
            :label="item.label"
            :value="item.value"
          >
          </el-option>
        </el-option-group>
      </el-select>

      <el-date-picker
        v-model="datevalue"
        type="date"
        placeholder=""
        value-format="yyyy-MM-dd"
        v-if="selectvalue == 'startDate'"
      >
      </el-date-picker>
      <span slot="footer" class="dialog-footer">
        <el-button @click="dialogSearchVisible = false"> </el-button>
        <el-button type="primary" @click="submitselect"> </el-button>
      </span>
    </el-dialog>
  </div>
</template>
<script>
import axios from "axios";
import globaldata from "../GlobalData/globaldata";

export default {
  created: function() {
    let that = this;
    axios.get("http://47.102.214.37:8080/user/me").then((res) => {
      console.log(res.data);
      that.userRole = res.data.role;
    });
    if (globaldata.taskselectInfo.length != 0) {
      that.selectInfo = globaldata.taskselectInfo;
      that.dynamicTags = globaldata.taskdynamicTags;
      that.search();
    } else {
      setTimeout(() => {
        that.refresh();
      }, 300);
    }

    // 
    axios({
      method: "GET",
      url: "http://47.102.214.37:8080/device?page=0&size=100",
    })
      .then((res) => {
        for (var i = 0; i < res.data.content.length; i++) {
          let obj = {};
          obj.value = res.data.content[i].id;
          obj.label =
            res.data.content[i].name +
            " (" +
            res.data.content[i].deviceNo +
            ")";
          that.deviceoptions[0].options.push(obj);
        }
      })
      .catch((err) => {
        console.log(err);
      });

    // OPERATOR
    axios
      .get("http://47.102.214.37:8080/user/query?role==OPERATOR")
      .then((res) => {
        console.log(res.data);
        for (let i = 0; i < res.data.content.length; i++) {
          that.opsoptions[0].options.push({
            value: res.data.content[i].id,
            label:
              res.data.content[i].name +
              " (" +
              res.data.content[i].username +
              ")",
          });
        }
      });
  },
  data() {
    return {
      userRole: "",
      tableData: [],
      checkedDetail: [],
      /*  */
      selectoptions: [
        {
          label: "",
          options: [
            {
              value: "name",
              label: "",
            },
            {
              value: "scheduleType",
              label: "",
            },
            {
              value: "device",
              label: "",
            },
            {
              value: "ops",
              label: "",
            },
            {
              value: "startDate",
              label: "",
            },
          ],
        },
      ],
      selectvalue: "",
      selectmodel: "",
      dialogSearchVisible: false,
      selectInfo: [],
      dynamicTags: [], // 

      // 
      scheduleTypevalue: "",
      scheduleTypeoptions: [
        {
          label: "",
          options: [
            {
              value: "Monthly",
              label: "",
            },
            {
              value: "Yearly",
              label: "",
            },
            {
              value: "Seasonly",
              label: "",
            },
            {
              value: "Daily",
              label: "",
            },
            {
              value: "Weeklt",
              label: "",
            },
          ],
        },
      ],

      // 
      devicevalue: "",
      deviceoptions: [
        {
          label: "",
          options: [],
        },
      ],

      // 
      opsvalue: "",
      opsoptions: [
        {
          label: "",
          options: [],
        },
      ],

      // 
      datevalue: "",

      // 
      currentPage: 1, //  
      page_size: 5, //  
      page: 1, // 
      size: 5, // 
      total: 0, // 
    };
  },
  methods: {
    //
    handleDetailSelectionChange(selection) {
      this.checkedDetail = selection;
      console.log(this.checkedDetail);
    },
    selectchange() {
      this.dialogSearchVisible = true;
    },
    submitselect() {
      this.dialogSearchVisible = false;
      if (this.selectvalue == "scheduleType") {
        this.selectInfo.push({
          ziduan: this.selectvalue,
          value: this.scheduleTypevalue,
        });
        this.dynamicTags.push(
          this.selectvalue + " / " + this.scheduleTypevalue
        );
      } else if (this.selectvalue == "device") {
        this.selectInfo.push({
          ziduan: this.selectvalue,
          value: this.devicevalue,
        });
        this.dynamicTags.push(this.selectvalue + " / " + this.devicevalue);
      } else if (this.selectvalue == "ops") {
        console.log(this.opsvalue);
        this.selectInfo.push({
          ziduan: this.selectvalue,
          value: this.opsvalue,
        });
        this.dynamicTags.push(this.selectvalue + " / " + this.opsvalue);
      } else if (this.selectvalue == "startDate") {
        console.log(this.opsvalue);
        this.selectInfo.push({
          ziduan: this.selectvalue,
          value: this.datevalue,
        });
        this.dynamicTags.push(this.selectvalue + " / " + this.datevalue);
      } else {
        this.selectInfo.push({
          ziduan: this.selectvalue,
          value: this.selectmodel,
        });
        this.dynamicTags.push(this.selectvalue + " / " + this.selectmodel);
      }
    },
    // 
    handleClose(tag) {
      let index = this.dynamicTags.indexOf(tag);
      console.log(index);
      this.dynamicTags.splice(index, 1);
      this.selectInfo.splice(index, 1);
    },
    // 
    search() {
      let that = this;
      let url = "";
      that.tableData = [];
      that.currentPage = 1;
      if (that.selectInfo.length == 0) {
        that.$message({
          message: "",
          type: "warning",
        });
      } else {
        // 
        if (that.selectInfo[0].ziduan == "name") {
          url =
            "http://47.102.214.37:8080/ops/query?" +
            that.selectInfo[0].ziduan +
            "=L" +
            that.selectInfo[0].value +
            "%25";
        } else {
          console.log(that.selectInfo[0]);
          // 
          if (
            that.selectInfo[0].ziduan == "device" ||
            that.selectInfo[0].ziduan == "ops"
          ) {
            if (that.selectInfo[0].value.length > 1) {
              url =
                "http://47.102.214.37:8080/ops/query?" +
                that.selectInfo[0].ziduan +
                "=I" +
                that.selectInfo[0].value[0];
              for (let i = 1; i < that.selectInfo[0].value.length; i++) {
                url = url + "," + that.selectInfo[0].value[i];
              }
            } else {
              url =
                "http://47.102.214.37:8080/ops/query?" +
                that.selectInfo[0].ziduan +
                "==" +
                that.selectInfo[0].value;
            }
          } else {
            url =
              "http://47.102.214.37:8080/ops/query?" +
              that.selectInfo[0].ziduan +
              "==" +
              that.selectInfo[0].value;
          }
        }
        if (that.selectInfo.length == 1) {
          // that.exporturl = url;
          axios.get(url).then((res) => {
            console.log(res.data);
            that.total = res.data.totalElements;
            if (res.data.content.length == 0) {
              that.$message({
                message: "",
                type: "warning",
              });
            } else {
              for (let i = 0; i < res.data.content.length; i++) {
                let obj = {};
                obj.id = res.data.content[i].id;
                obj.name = res.data.content[i].name;
                setTimeout(() => {
                  let URL =
                    "http://47.102.214.37:8080/ops/schedule/status/" +
                    res.data.content[i].id;
                  axios.get(URL).then((res) => {
                    if (res.data.nextDate == null) {
                      obj.nextDate = "";
                    } else {
                      obj.nextDate = res.data.nextDate;
                    }
                    if (res.data.nextDateDay == null) {
                      obj.deadline = "";
                    } else {
                      obj.deadline = res.data.nextDateDay;
                    }
                  });
                  setTimeout(() => {
                    that.tableData.push(obj);
                  }, 400);
                }, 300);
              }
              // 
              globaldata.taskselectInfo = that.selectInfo;
              globaldata.taskdynamicTags = that.dynamicTags;

              setTimeout(() => {
                that.$message({
                  message: "",
                  type: "success",
                });
              }, 300);
            }
          });
        } else {
          console.log(that.selectInfo);
          for (let i = 1; i < that.selectInfo.length; i++) {
            if (that.selectInfo[i].ziduan == "name") {
              url =
                url +
                "&" +
                that.selectInfo[i].ziduan +
                "=L" +
                that.selectInfo[i].value +
                "%25";
            } else {
              if (
                that.selectInfo[i].ziduan == "device" ||
                that.selectInfo[i].ziduan == "ops"
              ) {
                if (that.selectInfo[i].value.length > 1) {
                  url =
                    url +
                    "&" +
                    that.selectInfo[i].ziduan +
                    "=I" +
                    that.selectInfo[i].value[0];
                  for (let j = 1; j < that.selectInfo[i].value.length; j++) {
                    url = url + "," + that.selectInfo[i].value[j];
                  }
                } else {
                  url =
                    url +
                    "&" +
                    that.selectInfo[i].ziduan +
                    "==" +
                    that.selectInfo[i].value;
                }
              } else {
                url =
                  url +
                  "&" +
                  that.selectInfo[i].ziduan +
                  "==" +
                  that.selectInfo[i].value;
              }
            }
          }
          // that.exporturl = url;
          axios.get(url).then((res) => {
            console.log(res.data);
            that.tableData = [];
            that.total = res.data.totalElements;
            that.currentPage = 1;
            for (let i = 0; i < res.data.content.length; i++) {
              let obj = {};
              obj.id = res.data.content[i].id;
              obj.name = res.data.content[i].name;
              setTimeout(() => {
                let URL =
                  "http://47.102.214.37:8080/ops/schedule/status/" +
                  res.data.content[i].id;
                axios.get(URL).then((res) => {
                  if (res.data.nextDate == null) {
                    obj.nextDate = "";
                  } else {
                    obj.nextDate = res.data.nextDate;
                  }
                  if (res.data.nextDateDay == null) {
                    obj.deadline = "";
                  } else {
                    obj.deadline = res.data.nextDateDay;
                  }
                });
                setTimeout(() => {
                  that.tableData.push(obj);
                }, 400);
              }, 300);
            }
            // 
            globaldata.taskselectInfo = that.selectInfo;
            globaldata.taskdynamicTags = that.dynamicTags;

            setTimeout(() => {
              that.$message({
                message: "",
                type: "success",
              });
            }, 300);
          });
        }
      }
    },

    // 
    refresh() {
      let that = this;
      that.tableData = [];
      that.currentPage = 1;
      let index = 1;
      if (["ROOT", "ADMIN", "CREATOR", "SUPERVISOR"].includes(that.userRole)) {
        let url =
          "http://47.102.214.37:8080/ops/schedule?page=0&size=" +
          that.page_size;
        axios.get(url).then((res) => {
          console.log(res.data);
          that.total = res.data.totalElements;
          for (let i = 0; i < res.data.content.length; i++) {
            let obj = {};
            obj.opuser = "";
            obj.devicename = "";
            obj.deviceNo = "";
            obj.index = index++;
            obj.id = res.data.content[i].id;
            obj.taskname =
              res.data.content[i].name == null
                ? ""
                : res.data.content[i].name;
            let URL =
              "http://47.102.214.37:8080/ops/schedule/status/" +
              res.data.content[i].id;
            axios.get(URL).then((res) => {
              if (res.data.nextDate == null) {
                obj.nextDate = "";
              } else {
                obj.nextDate = res.data.nextDate;
              }
              if (res.data.nextDateDay == null) {
                obj.deadline = "";
              } else {
                obj.deadline = res.data.nextDateDay;
              }
            });
            // 
            setTimeout(() => {
              if (res.data.content[i].device.length == 0) {
                obj.devicename = "";
                obj.deviceNo = "";
              } else {
                for (let j = 0; j < res.data.content[i].device.length; j++) {
                  let url =
                    "http://47.102.214.37:8080/device/" +
                    res.data.content[i].device[j].id;
                  axios
                    .get(url)
                    .then((res) => {
                      obj.devicename += res.data.name + " / ";
                      obj.deviceNo += res.data.deviceNo + " / ";
                    })
                    .catch(() => {
                      obj.device = "";
                    });
                }
              }

              // 
              setTimeout(() => {
                if (res.data.content[i].ops.length == 0) {
                  obj.opuser = "";
                } else {
                  for (let k = 0; k < res.data.content[i].ops.length; k++) {
                    let searchops =
                      "http://47.102.214.37:8080/user/" +
                      res.data.content[i].ops[k].id;
                    axios
                      .get(searchops)
                      .then((res) => {
                        obj.opuser += res.data.name + " / ";
                      })
                      .catch(() => {
                        obj.opuser = "";
                      });
                  }
                }
                setTimeout(() => {
                  that.tableData.push(obj);
                }, 300);
              }, 300);
            }, 300);
          }
          setTimeout(() => {
            that.$message({
              message: "",
              type: "success",
            });
          }, 600);
        });
      } else {
        let URL = "http://47.102.214.37:8080/my/schedule";
        axios.get(URL).then((res) => {
          console.log(res.data);
          that.total = res.data.totalElements;
          for (let i = 0; i < res.data.content.length; i++) {
            let obj = {};
            obj.opuser = "";
            obj.devicename = "";
            obj.deviceNo = "";
            obj.index = index++;
            obj.name = res.data.content[i].name;
            let URL =
              "http://47.102.214.37:8080/ops/schedule/status/" +
              res.data.content[i].id;
            axios.get(URL).then((res) => {
              if (res.data.nextDate == null) {
                obj.nextDate = "";
              } else {
                obj.nextDate = res.data.nextDate;
              }
              if (res.data.nextDateDay == null) {
                obj.deadline = "";
              } else {
                obj.deadline = res.data.nextDateDay;
              }
            });
            // 
            setTimeout(() => {
              if (res.data.content[i].device.length == 0) {
                obj.devicename = "";
                obj.deviceNo = "";
              } else {
                for (let j = 0; j < res.data.content[i].device.length; j++) {
                  let url =
                    "http://47.102.214.37:8080/device/" +
                    res.data.content[i].device[j].id;
                  axios
                    .get(url)
                    .then((res) => {
                      console.log(res.data);
                      obj.devicename += res.data.name + " / ";
                      obj.deviceNo += res.data.deviceNo + " / ";
                    })
                    .catch(() => {
                      obj.device = "";
                    });
                }
              }

              // 
              setTimeout(() => {
                if (res.data.content[i].ops.length == 0) {
                  obj.opuser = "";
                } else {
                  for (let k = 0; k < res.data.content[i].ops.length; k++) {
                    let searchops =
                      "http://47.102.214.37:8080/user/" +
                      res.data.content[i].ops[k].id;
                    axios
                      .get(searchops)
                      .then((res) => {
                        console.log(res.data);
                        obj.opuser += res.data.name + " / ";
                      })
                      .catch(() => {
                        obj.opuser = "";
                      });
                  }
                }
                setTimeout(() => {
                  that.tableData.push(obj);
                }, 300);
              }, 300);
            }, 300);
          }
          setTimeout(() => {
            that.$message({
              message: "",
              type: "success",
            });
          }, 600);
        });
      }
      // 
      that.selectInfo = [];
      that.selectvalue = "";
      that.selectmodel = "";
      that.dynamicTags = [];
      globaldata.taskselectInfo = [];
      globaldata.taskdynamicTags = [];
    },
    // 
    handleDetail(index) {
      console.log(this.tableData[index]);
      this.$router.push({
        path: "/taskDetailInfo",
        query: this.tableData[index],
      });
    },
    // 
    handleEdit(index) {
      console.log(this.tableData[index]);
      let obj = {};
      obj.taskID = this.tableData[index].id;
      this.$router.push({
        path: "/editTask",
        query: obj,
      });
    },
    // 
    handleDelete(index) {
      let that = this;
      this.$confirm(", ?", "", {
        confirmButtonText: "",
        cancelButtonText: "",
        type: "warning",
      })
        .then(() => {
          let url =
            "http://47.102.214.37:8080/ops/schedule/" + that.taskData[index].id;
          axios.delete(url).then((res) => {
            if (res.status == 200) {
              that.taskData.splice(index, 1);
              this.$message({
                message: "",
                type: "success",
              });
            }
          });
        })
        .catch(() => {
          that.$message.info("");
        });
    },
    // 
    delectExtraInfo() {
      let that = this;
      if (this.checkedDetail.length == 0) {
        that.$alert("", "", {
          confirmButtonText: "",
        });
      } else {
        that
          .$confirm(", \n?", "", {
            confirmButtonText: "",
            cancelButtonText: "",
            type: "warning",
          })
          .then(() => {
            that.checkedDetail.forEach((element) => {
              that.tableData.forEach((e, i) => {
                if (element.id == e.id) {
                  console.log(element, e, i);
                  let url = "http://47.102.214.37:8080/ops/schedule/" + e.id;
                  axios.delete(url).then(() => {
                    this.$message({
                      message: "",
                      type: "success",
                    });
                    that.tableData.splice(i, 1);
                    that.checkedDetail.pop();
                    that.total--;
                  });
                }
              });
            });
          })
          .catch(() => {
            this.$message.info("");
          });
      }
    },
    // 
    handleSizeChange(val) {
      // console.log(` ${val} `);
      let that = this;
      let index = 1;
      that.tableData = [];
      that.currentPage = 1;
      console.log(val);
      that.size = val;
      that.page_size = val;
      let url =
        "http://47.102.214.37:8080/ops/schedule?page=0" + "&size=" + that.size;
      console.log(url);
      axios.get(url).then((res) => {
        console.log(res.data);
        that.total = res.data.totalElements;
