              >
            </el-button>
          </div>
          <div>
            <el-table
              :data="taskrecordtableData"
              stripe
              border
              style="width:100%;"
              class="extraTable"
            >
              <el-table-column type="expand">
                <template slot-scope="props">
                  <el-form
                    label-position="left"
                    inline
                    class="demo-table-expand"
                  >
                    <el-form-item label="">
                      <span>{{ props.row.fix }}</span>
                    </el-form-item>
                    <el-form-item label="">
                      <span>{{ props.row.record }}</span>
                    </el-form-item>
                    <el-form-item label="">
                      <span>{{ props.row.report }}</span>
                    </el-form-item>
                    <el-form-item label="">
                      <img :src="props.row.pic" v-if="props.row.ifpicexist" />
                      <span v-if="!props.row.ifpicexist"></span>
                    </el-form-item>
                  </el-form>
                </template>
              </el-table-column>
              <el-table-column
                prop="id"
                label="ID"
                width="70"
              ></el-table-column>
              <el-table-column
