              ></el-table-column>
              <el-table-column
                prop="hasException"
                label=""
              ></el-table-column>
              <el-table-column prop="opUser" label=""></el-table-column>
            </el-table>
            <!--  -->
            <div>
              <el-pagination
                @size-change="handleSizeChange"
                @current-change="handleCurrentChange"
                :current-page="currentPage"
                :page-sizes="[5, 10, 15]"
                :page-size="page_size"
                layout="sizes,total, prev, pager, next, jumper"
                :total="total"
              ></el-pagination>
            </div>
          </div>
        </el-tab-pane>
      </el-tabs>
    </div>
    <!--  -->
    <el-dialog title="" :visible.sync="dialogAddRecord">
      <el-form :model="form" ref="form" label-position="left">
        <el-form-item
          label=": "
          :label-width="formLabelWidth"
          prop="fix"
        >
          <el-input
            v-model="form.fix"
            placeholder=""
          ></el-input>
        </el-form-item>
        <el-form-item
          label=": "
          :label-width="formLabelWidth"
          prop="record"
        >
          <el-input
            v-model="form.record"
            autocomplete="off"
            placeholder=""
          ></el-input>
        </el-form-item>
        <el-form-item
          label=": "
          :label-width="formLabelWidth"
          prop="report"
        >
          <el-input
            v-model="form.report"
            autocomplete="off"
            placeholder=""
          ></el-input>
        </el-form-item>
        <el-form-item
          label=":"
          label-width="180px"
          prop="hasException"
        >
          <el-radio-group v-model="form.hasException">
            <el-radio label="true"></el-radio>
            <el-radio label="false"></el-radio>
          </el-radio-group>
        </el-form-item>
        <el-form-item
          label=":"
          :label-width="formLabelWidth"
          prop="pic"
        >
          <el-upload
            list-type="picture"
            action=""
            accept=".jpg, .png"
            :limit="1"
            :auto-upload="false"
            :file-list="fileList"
            :on-change="getFile"
          >
            <el-button size="small" type="primary"></el-button>
            <div slot="tip" class="el-upload__tip">jpg/png</div>
          </el-upload>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="dialogAddRecord = false"> </el-button>
        <el-button type="primary" @click="submitrecord"> </el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
import axios from "axios";
import User from "../components/Userinfo";

export default {
  created: function() {
    let that = this;
    that.taskid = that.$route.query.id;
    let url =
      "http://47.102.214.37:8080/ops/schedule/detail/" + that.$route.query.id;
    axios.get(url).then((res) => {
      console.log(res.data);
      // 
      that.name = res.data.name;
      if (that.scheduleType == null) {
        switch (res.data.scheduleType) {
          case "Yearly":
            that.scheduleType = "";
            break;
          case "Seasonally":
            that.scheduleType = "";
            break;
          case "Monthly":
            that.scheduleType = "";
            break;
          case "Weekly":
            that.scheduleType = "";
            break;
          case "Daily":
            that.scheduleType = "";
            break;
          case "Predictability":
            that.scheduleType = "";
            break;
          default:
            break;
        }
      }
      // 
      if (that.startDate == null) {
        that.startDate = res.data.startDate;
      }
      if (that.scheduleType == "") {
        that.scheduleType_info = "";
      }
      if (that.scheduleType == "") {
        switch (that.startDate) {
          case 1:
            that.scheduleType_info = "";
            break;
          case 2:
            that.scheduleType_info = "";
            break;
          case 3:
            that.scheduleType_info = "";
            break;
          case 4:
            that.scheduleType_info = "";
            break;
          case 5:
            that.scheduleType_info = "";
            break;
          case 6:
            that.scheduleType_info = "";
            break;
          case 7:
            that.scheduleType_info = "";
            break;
          default:
            break;
        }
      } else if (that.scheduleType == "") {
        that.scheduleType_info =
          " " + that.startDate.split("-")[2] + " ";
      } else if (that.scheduleType == "") {
        that.scheduleType_info =
          " " + that.startDate.split("-")[2] + " ";
      } else if (that.scheduleType == "") {
        that.scheduleType_info = "";
      } else if (that.scheduleType == "") {
        that.scheduleType_info = "";
      }
      // 
      if (that.side == null) {
        that.side = res.data.side;
      }
      // 
      if (that.acceptedStandard == null) {
        that.acceptedStandard = res.data.acceptedStandard;
      }
      // 
      if (res.data.content != "") {
        for (let a = 0; a < res.data.content.length; a++) {
          that.content.push({
            contentinfotitle: res.data.content[a].title,
            contentinfodetail: res.data.content[a].detail,
          });
        }
      }
      if (res.data.tools != "") {
        that.tools = res.data.tools;
      }
      if (res.data.remark != "") {
        that.remark = res.data.remark;
      }
      // 
      for (let i = 0; i < res.data.device.length; i++) {
        let searchdevice =
          "http://47.102.214.37:8080/device/" + res.data.device[i].id;
        axios.get(searchdevice).then((res) => {
          let obj = {};
          obj.id = res.data.id;
          obj.name = res.data.name;
          obj.brand = res.data.brand;
          obj.deviceNo = res.data.deviceNo;
          obj.clazz = res.data.clazz;
          that.devicetableData.push(obj);
        });
      }
      // 
      for (let i = 0; i < res.data.ops.length; i++) {
        let searchops = "http://47.102.214.37:8080/user/" + res.data.ops[i].id;
        axios.get(searchops).then((res) => {
          that.users.push({
            name: res.data.name,
            username: res.data.username,
            useremail: res.data.email,
          });
        });
      }
    });

    setTimeout(() => {
      // 
      let URL =
        "http://47.102.214.37:8080/ops/record/schedule/" +
        that.$route.query.id +
        "?page=0&size=" +
        that.page_size;
      axios.get(URL).then((res) => {
        console.log(res.data);
        that.total = res.data.totalElements;
        for (let i = 0; i < res.data.content.length; i++) {
          let obj = {};
          obj.id = res.data.content[i].id;
          res.data.content[i].date == null
            ? (obj.date = "")
            : (obj.date = res.data.content[i].date);
          res.data.content[i].realDate == null
            ? (obj.realDate = "")
            : (obj.realDate = res.data.content[i].realDate);
          res.data.content[i].fix == null
            ? (obj.fix = "")
            : (obj.fix = res.data.content[i].fix);
          res.data.content[i].hasException == false
            ? (obj.hasException = "")
            : (obj.hasException = "");
          res.data.content[i].record == null
            ? (obj.record = "")
            : (obj.record = res.data.content[i].record);
          res.data.content[i].report == null
            ? (obj.report = "")
            : (obj.report = res.data.content[i].report);
          obj.ifpicexist = true;
          res.data.content[i].pic == null
            ? (obj.ifpicexist = false)
            : (obj.pic =
                "http://47.102.214.37:8080/pic/" + res.data.content[i].pic);
          setTimeout(() => {
            if (res.data.content[i].opUser == null) {
              obj.opUser = "";
            } else {
              let searchops =
                "http://47.102.214.37:8080/user/" +
                res.data.content[i].opUser.id;
              axios
                .get(searchops)
                .then((res) => {
                  obj.opUser = res.data.name;
                })
                .catch(() => {
                  obj.opUser = "";
                });
            }
          }, 300);
          that.taskrecordtableData.unshift(obj);
        }
      });
    }, 300);
  },
  data() {
    return {
      taskid: "", //ID
      name: null, // 
      opUserid: "", // id
      tabsActiveName: "first", // 

      /*  */
      users: [], // 
      side: null, // 
      acceptedStandard: null, // 
      scheduleType: null, // 
      startDate: null, // 
      scheduleType_info: null, // 
      // 
      devicetableData: [],

      /*  */
      content: [], // 

      /*  */
      tools: null, // 

      /*  */
      remark: null, // 

      /*  */
      taskrecordtableData: [], // 
      // 
      dialogAddRecord: false,
      form: {
        fix: "",
        record: "",
        report: "",
        hasException: "",
        pic: "",
      },
      formLabelWidth: "150px",
      // 
      fileList: [],
      dialogVisible: false,
      dialogImageUrl: "",
      // 
      currentPage: 1, //  
      page_size: 5, //  
      page: 1, // 
      size: 5, // 
      total: 0, // 
    };
  },
  methods: {
    getFile(file) {
      this.getBase64(file.raw).then((res) => {
        const params = res.split(",");
        console.log(params, "params");
        if (params.length > 0) {
          this.form.pic = params[1];
        }
      });
    },
    // base64
    getBase64(file) {
      return new Promise(function(resolve, reject) {
        const reader = new FileReader();
        let imgResult = "";
        reader.readAsDataURL(file);
        reader.onload = function() {
          imgResult = reader.result;
        };
        reader.onerror = function(error) {
          reject(error);
        };
        reader.onloadend = function() {
          resolve(imgResult);
        };
      });
    },

    // 
    addrecord() {
      this.dialogAddRecord = true;
    },
    submitrecord() {
      let that = this;
      axios.get("http://47.102.214.37:8080/user/me").then((res) => {
        console.log(res.data);
        that.opUserid = res.data.id;
      });
      setTimeout(() => {
        if (
          that.form.fix == "" ||
          that.form.record == "" ||
          that.form.report == "" ||
          that.form.hasException == "" ||
          that.form.pic == ""
        ) {
          that.$message({
            message: "",
            type: "warning",
          });
        } else {
          let obj = {};
          let picurl = "http://47.102.214.37:8080/pic";
          obj.fix = that.form.fix;
          obj.record = that.form.record;
          obj.report = that.form.report;
          if (that.form.hasException == "false") {
            obj.hasException = false;
          } else {
            obj.hasException = true;
          }
          obj.schedule = { id: that.$route.query.id * 1 };
          obj.opUser = { id: that.opUserid };
          setTimeout(() => {
            axios({
              url: picurl,
              method: "post",
              data: that.form.pic,
              headers: {
                "Content-Type": "text/plain",
              },
            }).then((res) => {
              console.log(res);
              obj.pic = res.data;
            });
            setTimeout(() => {
              console.log(obj);
              axios
                .post("http://47.102.214.37:8080/ops/record", obj)
                .then((res) => {
                  console.log(res);
                  that.$message({
                    message: "",
                    type: "success",
                  });
                  that.dialogAddRecord = false;
                })
                .catch((res) => {
                  console.log(res.response);
                });
            }, 300);
          }, 300);
        }
      }, 300);
    },
    // 
    handleSizeChange(val) {
      // console.log(` ${val} `);
      let that = this;
      console.log(val);
      that.size = val;
      let url =
        "http://47.102.214.37:8080/ops/record/schedule/" +
        that.$route.query.id +
        "?page=0" +
        "&size=" +
        that.size;
      console.log(url);
      axios.get(url).then((res) => {
        console.log(res.data);
        that.taskrecordtableData = [];
        that.total = res.data.totalElements;
        for (let i = 0; i < res.data.content.length; i++) {
          let obj = {};
          obj.id = res.data.content[i].id;
          res.data.content[i].date == null
            ? (obj.date = "")
            : (obj.date = res.data.content[i].date);
          res.data.content[i].realDate == null
            ? (obj.realDate = "")
            : (obj.realDate = res.data.content[i].realDate);
          res.data.content[i].fix == null
            ? (obj.fix = "")
            : (obj.fix = res.data.content[i].fix);
          res.data.content[i].hasException == false
            ? (obj.hasException = "")
            : (obj.hasException = "");
          res.data.content[i].record == null
            ? (obj.record = "")
            : (obj.record = res.data.content[i].record);
          res.data.content[i].report == null
            ? (obj.report = "")
            : (obj.report = res.data.content[i].report);
          obj.ifpicexist = true;
          res.data.content[i].pic == null
            ? (obj.ifpicexist = false)
            : (obj.pic =
                "http://47.102.214.37:8080/pic/" + res.data.content[i].pic);
          that.taskrecordtableData.unshift(obj);
        }
        setTimeout(() => {
          that.$message({
            message: "",
            type: "success",
          });
        }, 300);
      });
    },
    // // 
    handleCurrentChange(val) {
      let that = this;
      that.page = val;
      that.currentPage = val;
      console.log(val);
      let url =
        "http://47.102.214.37:8080/ops/record/schedule/" +
        that.$route.query.id +
        "?page=" +
        (that.page - 1) +
        "&size=" +
        that.size;
      console.log(url);
      axios.get(url).then((res) => {
        console.log(res.data);
        that.taskrecordtableData = [];
        that.total = res.data.totalElements;
        for (let i = 0; i < res.data.content.length; i++) {
          let obj = {};
          obj.id = res.data.content[i].id;
          res.data.content[i].date == null
            ? (obj.date = "")
            : (obj.date = res.data.content[i].date);
          res.data.content[i].realDate == null
            ? (obj.realDate = "")
            : (obj.realDate = res.data.content[i].realDate);
          res.data.content[i].fix == null
            ? (obj.fix = "")
            : (obj.fix = res.data.content[i].fix);
          res.data.content[i].hasException == false
            ? (obj.hasException = "")
            : (obj.hasException = "");
          res.data.content[i].record == null
            ? (obj.record = "")
            : (obj.record = res.data.content[i].record);
          res.data.content[i].report == null
            ? (obj.report = "")
            : (obj.report = res.data.content[i].report);
          obj.ifpicexist = true;
          res.data.content[i].pic == null
            ? (obj.ifpicexist = false)
            : (obj.pic =
                "http://47.102.214.37:8080/pic/" + res.data.content[i].pic);
          that.taskrecordtableData.unshift(obj);
        }
        setTimeout(() => {
          that.$message({
            message: "",
            type: "success",
          });
        }, 300);
      });
    },
  },
  components: {
    User,
  },
};
</script>

